.select_selected {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 100%;
    width: 13px;
    height: 100%;
  }
}
.display3 {
  font-weight: 700;
  font-size: 38px;
  line-height: 120%;
}
.btn-yellow {
  font-size: 11px;
  line-height: 120%;
  background: #EBA707;
  border-radius: 29px;
  color: #fff;
  text-transform: uppercase;
  &:hover {
    background: #000;
    border: 1px solid #EBA707;
  }
}
.btn-gray {
  background: #7A7A7A;
  border-radius: 29px;
  border: 1px solid #FFFFFF;
  font-size: 11px;
  line-height: 120%;
  text-transform: uppercase;
  color: #FFFFFF;
  &:hover {
    background: #EBA707;
  }
}
.offers-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 18px;
}
.offers-card {
  position: relative;
  &:before {
    content: '';
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    opacity: 0;
    transition: all 0.5s;
  }
  &:hover {
    &:before {
      opacity: 1;
    }
  }
  &__desc {
    position: relative;
    z-index: 4;
  }
  &__img {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__icon {
    position: absolute;
    left: 25px;
    bottom: 25px;
    width: 82px;
    height: auto;
    display: flex;
    img {
      width: auto;
      height: auto;
    }
  }
  &__title {
    font-weight: 300;
    font-size: 32px;
    text-align: end;
    position: absolute;
    left: 25px;
    right: 25px;
    bottom: 25px;
    span {
      font-size: 18px;
      line-height: 120%;
      font-weight: 400;
      display: block;
      margin-top: 5px;
    }
  }
}
