@media screen and (max-width: 1370px) {
  .offers-card {
    &__title {
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .offers-cards {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 992px) {
  .display3 {
    font-size: 28px;
  }
  .offers-card {
    &__icon {
      width: 35px;
      height: auto;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &__title {
      font-size: 20px;
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .display3 {
    font-size: 24px;
  }
  .offers-cards {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 576px) {
  .offers-card {
    &__desc {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;
    }
    &__icon {
      justify-content: center;
      position: static;
      top: unset;
      left: unset;
      width: 40px;
      height: auto;
      min-height: 50px;
      margin: 0 auto;
    }
    &__title {
      font-size: 18px;
      position: static;
      top: unset;
      left: unset;
      text-align: center;
      padding-top: 7px;
      min-height: 85px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      span {
        font-size: 14px;
      }
    }
    &__title {
      br {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 375px) {
  .offers-cards {
    grid-template-columns: 1fr;
  }
  .offers-card {
    &__icon {
      width: 70px;
    }
  }
}